<template>
    <t-page class="dd-root">
        <div slot="header">
            <div class="header">
                <t-header-new>
                    <div slot="center" class="header-title">文章</div>
                </t-header-new>
            </div>
            
        </div>
        <div slot="container" class="container1">
            <p>
                一日之计在于晨，随着生活水平的提高，大家对早餐也是越来越重视了。早餐不仅要吃饱，更要吃好，营养才是关键。
            </p>
            <p>今天 小编就给大家分享一种美味的早餐饼，一起来看一下吧</p>
            <img src="static/imgs/discovery-detail1.jpg" alt="">
            
            <h3>做法:</h3>

            <p>1.粉丝提前用凉水泡上，凉水泡软后，再用热水泡几分钟</p>

            <img src="static/imgs/discovery-detail2.jpg" alt="">
            <p>2.鸡蛋4个打入碗中搅拌均匀备用</p>

            <img src="static/imgs/discovery-detail3.jpg" alt="">
            <p>3.青椒切成小丁</p>

            <img src="static/imgs/discovery-detail4.jpg" alt="">
            <p>4.胡萝卜擦丝后切成小丁</p>

            <img src="static/imgs/discovery-detail5.jpg" alt="">
            <p>5.粉丝捞出控水后切成小段</p>

            <img src="static/imgs/discovery-detail6.jpg" alt="">
            <p>6.把切好的粉丝、胡萝卜和青椒，放入盛有蛋液的大碗中，搅拌均匀</p>

            <img src="static/imgs/discovery-detail7.jpg" alt="">
            <p>7.放少许面粉起到粘合的作用，放一勺盐，适量十三香搅拌均匀即可</p>

            <img src="static/imgs/discovery-detail8.jpg" alt="">
            <p>8.平底锅烧热倒入食用油，油热后，倒入粉丝鸡蛋液，转动锅摊平</p>

            <img src="static/imgs/discovery-detail9.jpg" alt="">
            <p>9.全程小火，煎至两面金黄即可出锅</p>

            <img src="static/imgs/discovery-detail10.jpg" alt="">
            <p>你学会了吗？喜欢小编的一定要关注、分享加评论互动哦!</p>
        </div>
        
    </t-page>
</template>

<script>
    export default {
        data() {
            return {
            }
        },
    }
</script>

<style lang='scss' scoped>
.dd-root{
    .header{
        border-bottom: 1px solid #ddd;
        .header-title{
            font-size: 5.3vw;
            
        }
    }
    
    .container1{
        margin: auto;
        width: 92vw;
        overflow: hidden;
        h3{
            text-align: start;
        }
        img{
            margin: 0 auto;
            display: block;
            max-width: 88%;
        }
        p{
            text-align: start;
        }
    }
    
}
</style>


